/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.modal-overlay.open {
  opacity: 1;
  visibility: visible;
}

/* Modal Container */
.modal-container {
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow: hidden;
  transform: scale(0.9) translateY(20px);
  transition: all 0.3s ease;
  position: relative;
}

.modal-overlay.open .modal-container {
  transform: scale(1) translateY(0);
}

/* RTL Support */
.modal-container.rtl {
  direction: rtl;
}

/* Modal Header */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  background: #D4AF5F;;
  color: #1B1D21;
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1B1D21;
  margin: 0;
  order: 1;
}

.close-btn {
  background: none;
  border: none;
  color: #1B1D21;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  order: 2;
}

.close-btn:hover {
  background-color: rgba(27, 29, 33, 0.1);
  color: #1B1D21;
}

/* RTL Support for Modal Header */
.modal-container.rtl .modal-header {
  flex-direction: row-reverse;
}

.modal-container.rtl .modal-title {
  order: 2;
}

.modal-container.rtl .close-btn {
  order: 1;
}

/* Modal Body */
.modal-body {
  padding: 2rem;
  max-height: 60vh;
  overflow-y: auto;
}

/* Error Message */
.error-message {
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 0.875rem;
}

.error-icon {
  flex-shrink: 0;
  color: #dc2626;
}

/* Form Styles */
.notification-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  font-weight: 600;
  color: #374151;
  font-size: 0.875rem;
}

.form-input,
.form-textarea {
  padding: 0.75rem;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.2s ease;
  font-family: inherit;
}

.form-input:focus,
.form-textarea:focus {
  outline: none;
  border-color: #D4AA3A;
  box-shadow: 0 0 0 3px rgba(212, 170, 58, 0.1);
}

.form-textarea {
  resize: vertical;
  min-height: 100px;
}

/* User Selection Section */
.selected-users {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.selected-user-tag {
  background: linear-gradient(135deg, #D4AA3A 0%, #B68A35 100%);
  color: white;
  padding: 0.5rem 0.75rem;
  border-radius: 20px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
}

.remove-user-btn {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 0.125rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s ease;
}

.remove-user-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

/* User Search */
.user-search {
  margin-bottom: 1rem;
}

.search-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.search-icon {
  position: absolute;
  left: 1rem;
  color: #9ca3af;
  z-index: 1;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.5rem;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.2s ease;
}

.search-input:focus {
  outline: none;
  border-color: #D4AA3A;
  box-shadow: 0 0 0 3px rgba(212, 170, 58, 0.1);
}

/* Users List */
.users-list {
  max-height: 300px;
  overflow-y: auto;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  background: #f9fafb;
}

/* Loading State */
.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: #6b7280;
}

.loading-spinner {
  animation: spin 2s linear infinite;
  color: #D4AA3A;
  margin-bottom: 0.5rem;
}

.loading-text {
  font-size: 0.875rem;
  margin: 0;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* User Item */
.user-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-bottom: 1px solid #e5e7eb;
  cursor: pointer;
  transition: all 0.2s ease;
  background: white;
}

.user-item:last-child {
  border-bottom: none;
}

.user-item:hover {
  background-color: #f8fafc;
}

.user-item.selected {
  background: linear-gradient(135deg, #D4AA3A 0%, #B68A35 100%);
  color: white;
}

.user-item.selected .user-email,
.user-item.selected .user-department {
  color: rgba(255, 255, 255, 0.8);
}

/* User Avatar */
.user-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  background: linear-gradient(135deg, #D4AA3A 0%, #B68A35 100%);
  color: white;
  font-weight: 600;
  font-size: 0.875rem;
}

.avatar-image {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.avatar-text {
  font-size: 0.875rem;
  font-weight: 600;
}

/* User Info */
.user-info {
  flex: 1;
  min-width: 0;
}

.user-name {
  font-weight: 600;
  margin-bottom: 0.25rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.user-email {
  font-size: 0.875rem;
  color: #6b7280;
  margin-bottom: 0.25rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.user-department {
  font-size: 0.75rem;
  color: #9ca3af;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Selection Indicator */
.selection-indicator {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.check-icon {
  color: #D4AA3A;
}

.user-item.selected .check-icon {
  color: white;
}

/* Empty State */
.empty-users {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: #9ca3af;
}

.empty-icon {
  margin-bottom: 1rem;
  opacity: 0.5;
}

.empty-text {
  font-size: 0.875rem;
  margin: 0;
  text-align: center;
}

/* Modal Footer */
.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1.5rem 2rem;
  border-top: 1px solid #e5e7eb;
  background: #f9fafb;
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-width: 120px;
  justify-content: center;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-secondary {
  background-color: transparent;
  color: #1B1D21 !important;
  border: 1px solid #1B1D21;
}

.btn-secondary:hover:not(:disabled) {
  background-color: #1B1D21;
  color: #FFFFFF !important;
}

.btn-primary {
  background: linear-gradient(135deg, #D4AA3A 0%, #B68A35 100%);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(212, 170, 58, 0.3);
}

.btn-icon {
  flex-shrink: 0;
}

.loading-spinner-small {
  animation: spin 2s linear infinite;
  color: currentColor;
}

/* Responsive Design */
@media (max-width: 768px) {
  .modal-container {
    width: 95%;
    margin: 1rem;
  }
  
  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 1rem;
  }
  
  .modal-title {
    font-size: 1.25rem;
  }
  
  .btn {
    padding: 0.625rem 1.25rem;
    font-size: 0.875rem;
    min-width: 100px;
  }
  
  .users-list {
    max-height: 250px;
  }
}

/* RTL Specific Styles */
.modal-container.rtl .search-icon {
  left: auto;
  right: 1rem;
}

.modal-container.rtl .search-input {
  padding-left: 1rem;
  padding-right: 2.5rem;
}

.modal-container.rtl .modal-footer {
  justify-content: flex-start;
}

.modal-container.rtl .btn {
  flex-direction: row-reverse;
}
